/*
 * # Stylesheets
 * Each block lives in its own stylesheet to keep things separate and easy to
 * manage. This site uses BEM-style class names.
 */

/*
 * ## Site-Wide and Config Styles
 */

/*
 * ## Media Queries
 */

* {
  margin: 0;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*+* {
  margin-top: 15px;
}

body {
  margin-top: 0;
  background-color: #fff4e7;
}

body {
  font-family: futura-pt, futura, sans-serif;
  font-size: 16.06px;
  font-weight: 300;
  line-height: 1.5;
}

@media (min-width: 768px) {

  body {
    font-size: 22px;
  }
}

.wp-caption {
  max-width: 100% !important;
}

.wp-caption img {
  max-width: 100%;
  height: auto;
}

.wp-caption-text,.wp-caption-dd {
  display: block;
  margin-top: 0;
  padding: 0 5px;
  color: rgb(142, 131, 123);
  font-size: 80%;
  font-style: italic;
  line-height: 1.25;
}

.aligncenter,.alignleft,.alignright {
  display: block;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
}

figure.aligncenter, figure.alignleft, figure.alignright {
  margin-top: 30px;
}

img.aligncenter,.aligncenter>img,img.alignleft,.alignleft>img,img.alignright,.alignright>img {
  border: 1px solid rgba(104, 90, 79, 0.5);
  box-shadow: 0 1px 4px rgba(104, 90, 79, 0.25);
}

figure.aligncenter,dl.aligncenter {
  margin-top: 30px;
}

@media (min-width: 800px) {

  figure.aligncenter,dl.aligncenter {
    margin-left: -65px;
    margin-right: -65px;
    text-align: center;
  }

  figure.aligncenter img, dl.aligncenter img {
    max-width: 780px;
    width: 780px;
  }

  figure.aligncenter figcaption.wp-caption-text,figure.aligncenter dd.wp-caption-dd,dl.aligncenter figcaption.wp-caption-text,dl.aligncenter dd.wp-caption-dd {
    width: 780px;
    max-width: 780px;
  }
}

img.alignleft,img.alignright,img.aligncenter {
  max-width: 100%;
  height: auto;
}

img.aligncenter {
  margin-top: 30px;
}

@media (min-width: 800px) {

  img.aligncenter {
    margin-left: -65px;
    margin-right: -65px;
    max-width: 780px;
    width: 780px;
  }
}

.alignleft,figure.alignleft,img.alignleft,dl.alignleft,.alignright,figure.alignright,img.alignright,dl.alignright {
  margin-top: 30px;
}

@media (min-width: 768px) {

  .alignleft,figure.alignleft,img.alignleft,dl.alignleft,.alignright,figure.alignright,img.alignright,dl.alignright {
    max-width: 45% !important;
  }
}

@media (min-width: 768px) {

  .alignleft,figure.alignleft,img.alignleft,dl.alignleft {
    float: left;
    margin-right: 30px;
  }
}

@media (min-width: 800px) {

  .alignleft,figure.alignleft,img.alignleft,dl.alignleft {
    margin-left: -65px;
  }
}

@media (min-width: 768px) {

  .alignright,figure.alignright,img.alignright,dl.alignright {
    float: right;
    margin-left: 30px;
    text-align: right;
  }
}

@media (min-width: 800px) {

  .alignright,figure.alignright,img.alignright,dl.alignright {
    margin-right: -65px;
  }
}

.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

.posts-navigation {
  max-width: 1024px;
  margin-top: 6vh;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
  line-height: 1;
}

.posts-navigation .nav-links {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-content: space-between;
      -ms-flex-line-pack: justify;
          align-content: space-between;
}

.posts-navigation .nav-previous,.posts-navigation .nav-next {
  -webkit-box-flex: 1;
  -webkit-flex: 1 50%;
      -ms-flex: 1 50%;
          flex: 1 50%;
  margin: 0;
}

.posts-navigation .nav-previous a, .posts-navigation .nav-next a {
  color: rgb(147, 77, 114);
  font-size: 65%;
  font-weight: 700;
  letter-spacing: 0.2em;
  line-height: 1;
  text-decoration: none;
  text-transform: uppercase;
}

.posts-navigation .nav-next {
  text-align: right;
}

.search-form {
  min-width: 260px;
  margin: 0;
  text-align: center;
}

.search-field,.search-submit {
  display: inline-block;
  font-size: 16px;
  vertical-align: baseline;
}

.search-field {
  width: 60%;
  padding: 0.25em 0.5em;
  background: transparent;
  border: 0;
  border-bottom: 2px solid #020815;
  color: #020815;
  font-family: futura-pt, futura, sans-serif;
  font-weight: 300;
  letter-spacing: 0.1em;
  line-height: 1.5;
  -webkit-transition: 200ms all ease-in-out;
          transition: 200ms all ease-in-out;
}

.search-field::-webkit-input-placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(2, 8, 21, 0.4);
}

.search-field::-moz-placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(2, 8, 21, 0.4);
}

.search-field:-ms-input-placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(2, 8, 21, 0.4);
}

.search-field::placeholder {
  color: inherit;
  text-shadow: 0 0 4px rgba(2, 8, 21, 0.4);
}

.search-field:focus,.search-field:hover {
  outline: none;
  background-color: rgba(215, 206, 213, 0.25);
  border-color: #640035;
}

.search-submit {
  margin: 0;
  padding: 0.25em 0.5em;
  background: transparent;
  border: 3px solid #020815;
  border-radius: 7.5px;
  color: #020815;
  cursor: pointer;
  font-family: futura-pt, futura, sans-serif;
  font-weight: 800;
  letter-spacing: 0.1em;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: 200ms all ease-in-out;
          transition: 200ms all ease-in-out;
}

.search-submit:hover,.search-submit:active,.search-submit:focus {
  outline: none;
  background: rgba(255, 253, 249, 0.5);
  box-shadow: 0 0 10px rgba(2, 8, 21, 0.25);
  text-shadow: 0 0 5px rgba(2, 8, 21, 0.25);
}

