.gform_wrapper .ginput_container input:focus,.gform_wrapper .ginput_container input:hover,.gform_wrapper .ginput_container select:focus,.gform_wrapper .ginput_container select:hover,.gform_wrapper .ginput_container textarea:focus,.gform_wrapper .ginput_container textarea:hover {
